$offblack: #222;
$gray4: #BCBCBC;
$gray3: #D3D3D3;
$gray2: #E8E8E8;
$gray1: #F8F8F8;
$white: #FFF;

$blue4: #00007c;
$blue3: #007EFF;
$blue2: #2EB8FF;
$blue1: #AEF7FF;

$red3: #930000;
$red2: #E62828;
$red1: #E62828;

$green3: #00896A;
$green2: #33BA98;
$green1: #A0F2B8;

$purple3: #5700D3;
$purple2: #8D45FF;
$purple1: #BAA4FF;

$yellow3: #E0B500;
