//////////////////////////
// TOOLTIPS 					 ///
//////////////////////////
#tooltip {
  font-family: $fonts;
  background: rgba(255,255,255,.9);
  backdrop-filter: blur(5px);
  border-radius: .3rem;
  color: $offblack;
  display: block;
  font-size: 14px;
  font-weight: 400;
  padding: .2rem .4rem;
  position: absolute;
  white-space: pre;
  /*max-width: 180px;*/
  z-index: 300;
  visibility: hidden;
  border: 1px solid $offblack;
  white-space: pre-wrap;

	div {
		margin: 0;
	  padding: 0;
	}
	.row > * {
		display: inline-block;
	}
	.bold {
		font-weight: 600;
	}
}

